.projects {

  &__title {
    @extend.heading-2;
    margin: 0 0 30px 0;
    color: $color-brand500;
    font-weight: 700;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
}

.swiper {

  &-wrapper {
    margin-bottom: 10px;

    @include mediaTablet {
      margin-bottom: 40px;
    }
  }

  &-slide {
    height: auto;
  }

  &-buttons {
    display: flex;
    justify-content: center;
    gap: 100px;
    margin: 10px 0;
  }

  &-button-prev,
  &-button-next {
    position: static;
    height: auto;
    margin-top: 0;
    color: $color-brand;
    &::after {
      display: none;
    }
  }
}


