.hero {
  margin: 0 auto;
  padding: 40px 0;



  &__body {
    margin: 0 auto;

    @include mediaTablet {
      max-width: 510px;
    }
  }

  &__title {
    margin: 0 0 30px 0;
    font-size: 32px;
  }

  &__text {
    margin: 0 0 30px 0;
  }

  &__links {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 10px;

    @include mediaTablet {
      flex-wrap: nowrap;
    }
  }

  &__button {
    width: 160px;
  }

  &__social {

    &-list {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 30px;
    }

    &-link {
      padding: 2px 5px;
      color: $color-brand500;
      font-size: 24px;
      transition: color $transition-duration;

      &:hover {
        background-color: $color-brand;
        color: $color-white;
      }
    }
  }

}
