.button {
  position: relative;
  padding: 5px;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  transition: all $transition-duration;
  cursor: pointer;

  i {
    margin: 0 5px 0 0;
    color: $color-dark;
    transition: all $transition-duration;
  }

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: $color-brand;
    transition: all $transition-duration;
    z-index: -1;
  }

  &__link {
    color: $color-dark;
    transition: all $transition-duration;
  }

  &:hover {
    color: $color-white;

    &::after {
      height: 100%;
    }

    .button__link {
      color: $color-white;
    }

    i {
      color: $color-white;
    }
  }

}
