.body {
  position: relative;
  font-family: "Inter", sans-serif;

  &.lock {
    overflow: hidden;
  }

  @include mediaTablet {
    background-image: url('../images/background-image-1.png');
    background-repeat: no-repeat;
    background-position: 0 -60px;
    background-size: 30% auto;
  }
}

* {
  scroll-behavior: smooth;
}
