.card {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  border: 2px solid $color-brand500-light;
  border-radius: 8px;


  // @include mediaTablet {
  //   max-width: 400px;
  // }

  &__image,
  &__title {
    margin: 0 0 10px 0;
  }

  &__body {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 0 10px 15px 10px;
  }

  &__image {
    position: relative;
    margin: 0 0 10px 0;
    max-height: 200px;
    overflow: hidden;

    @include mediaTablet {
      max-height: 300px;
    }

    &::after {
      content:'';
      display: block;
      width: 100%;
      padding: 0 0 100%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    @extend .heading-3;
    color: $color-brand;

  }

  &__buttons {
    display: flex;
    gap: 30px;

  }

  &__badges {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    margin: 0 0 auto 0;
  }

}
