.contact-form {

  &__field {
    position: relative;
    border: 1px solid $color-dark500-light;
    margin: 0 0 25px 0;
    border-radius: 5px;
    padding: 5px 3px;

    &--text-area {
      min-height: 80px;
    }
  }

  input {
    width: 100%;
    @extend .text-1;
  }

  label {
    position: absolute;
    top: 0;
    left: 5px;
    background-color: #fff;
    padding: 0 5px;
    @include textTypohraphy(12, 1, 400);
    color: $color-dark500;
    transform: translateY(-50%);
  }

  textarea {
    width: 100%;
    height: 100%;
    resize: none;
  }

  &__button {
    display: block;
    margin-left: auto;
  }
}

.contact {
  &__body {
    margin: 0 auto;
    max-width: 435px;
  }

  &__title {
    @extend .heading-2;
    text-align: right;
    margin: 0 0 30px 0;
  }

  .field-form {
    height: 33px;
  }
}


