.header {
  display: flex;
  align-items: center;
  height: 50px;

  &__content {
    position: relative;
    display: flex;
    margin-left: auto;
  }

  &__burger {
    margin-left: auto;
  }

  &__menu {
    position: absolute;
    left: -110%;
    width: 100%;
    top: 100%;
    transition: all $transition-duration;
    background-color: $color-white;
    opacity: 0.98;
    z-index: 1;

    &.active {
      left: 0;
    }
  }

  @include mediaTablet {
    margin: 0 0 40px 0;

    &__menu {
      position: static;
      margin-left: auto;
      width: max-content;
    }

    .list-menu {
      display: flex;
      gap: 40px;
    }

  }
}

