.burger {
  position: relative;
  width: 20px;
  height: 16px;
  cursor: pointer;

  &__line,
  &::before,
  &::after {
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $color-brand;
    transition: all $transition-duration;
  }

  &__line {
    top: calc(50% - 1px);
  }

  &::before,
  &::after {
    content: '';
    display: block;
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }

  &.active {

    .burger__line {
      transform: scale(0);
    }

    &::before,
    &::after {
      background-color: $color-brand500;
    }

    &::before {
      top: 8px;
      transform: rotate(45deg);
    }

    &::after {
      bottom: 6px;
      transform: rotate(-45deg);
    }
  }

  @include mediaTablet {
    display: none;
  }
}
