.about {

  &__body {
    margin: 0 0 40px 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 50px;

    @media (min-width: 576px) {
      flex-wrap: nowrap;
    }
  }

  &__image {
    position: relative;
    flex: 0 1 auto;
    max-height: 440px;
    border-radius: 20px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__info {
    flex: 0 1 auto;
  }

  &__upper {
    @extend .heading-3;
  }

  &__title {
    @extend .heading-2;
    color: $color-brand;
  }

  &__paragraph {
    @extend .text-1;
    color: $color-dark;
  }

  &__tagline {
    @extend .heading-3;
    color: $color-brand500;
  }

  &__title,
  &__paragraph:not(:last-child),
  &__tagline {
    margin: 0 0 16px 0;
  }

  &__badges {
    &-title {
      @extend .heading-3;
      color: $color-dark;
      margin: 0 0 15px 0;
    }

    &-list {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
    }
  }
}
